
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0 /* 消除avatar和content并排时中间的空白字符 */;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .content .support {
  font-size: 0;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .support .icon.special {
    background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .support .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  line-height: 24px;
  height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  vertical-align: top;
  font-size: 10px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  margin-left: 2px;
  line-height: 24px;
  font-size: 10px;
}
.header .bulletin-wrapper {
  position: relative;
  line-height: 28px;
  height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(7,17,27,0.2);
/*font-size: 0  span不换行就不会有留白，设置此会使得...失效*/
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  margin-top: 8px;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  right: 12px;
  top: 8px;
  font-size: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px) /* 在ios手机上设置的背景模糊 */;
          backdrop-filter: blur(10px) /* 在ios手机上设置的背景模糊 */;
}
.header .detail .detail-wrapper {
  width: 100%;
  min-height: 100% /* 最小高度为视口高度 */;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px /* 防止内容过多会遮盖页脚 */;
}
.header .detail .detail-wrapper .detail-main .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail-wrapper .detail-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 28px auto 24px;
}
.header .detail .detail-wrapper .detail-main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.header .detail .detail-wrapper .detail-main .title .text {
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px;
}
.header .detail .detail-wrapper .detail-main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .support-item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:last-child {
  margin-bottom: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.decrease {
  background-image: url("decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.decrease {
    background-image: url("decrease_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.discount {
  background-image: url("discount_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.discount {
    background-image: url("discount_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
  background-image: url("guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.guarantee {
    background-image: url("guarantee_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
  background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.invoice {
    background-image: url("invoice_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.special {
  background-image: url("special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrapper .detail-main .supports .support-item .icon.special {
    background-image: url("special_2@3x.png");
}
}
.header .detail .detail-wrapper .detail-main .supports .support-item .text {
  line-height: 16px;
  font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin .content {
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto;
  clear: both;
  font-size: 32px;
}
.header .detail.fade-enter-active,
.header .detail.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  background: rgba(7,17,27,0.8);
}
.header .detail.fade-enter,
.header .detail.fade-leave-active {
  opacity: 0;
  background: rgba(7,17,27,0);
}


