
.food-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
}
.food-wrapper.move-enter-active,
.food-wrapper.move-leave-active {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food-wrapper.move-enter,
.food-wrapper.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food-wrapper .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100% /* 通过"padding-top: 100%"可以设置宽=高=100%的盒子 */;
}
.food-wrapper .food-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-wrapper .food-content .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food-wrapper .food-content .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food-wrapper .food-content .content {
  position: relative;
  padding: 18px;
}
.food-wrapper .food-content .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-wrapper .food-content .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
  height: 10px;
}
.food-wrapper .food-content .content .detail .sell-count,
.food-wrapper .food-content .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food-wrapper .food-content .content .detail .sell-count {
  margin-right: 12px;
}
.food-wrapper .food-content .content .price {
  font-weight: 700;
  line-height: 24px;
  font-size: 0;
}
.food-wrapper .food-content .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food-wrapper .food-content .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food-wrapper .food-content .content .cart-control-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food-wrapper .food-content .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
}
.food-wrapper .food-content .content .buy.fade-enter-active,
.food-wrapper .food-content .content .buy.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.food-wrapper .food-content .content .buy.fade-enter,
.food-wrapper .food-content .content .buy.fade-leave-active {
  opacity: 0;
}
.food-wrapper .food-content .info {
  padding: 18px;
}
.food-wrapper .food-content .info .title {
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-wrapper .food-content .info .text {
  padding: 0 8px;
  line-height: 24px;
  font-size: 12px;
  color: #4d555d;
}
.food-wrapper .food-content .rating {
  padding-top: 18px;
}
.food-wrapper .food-content .rating .title {
  margin-left: 18px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-wrapper .food-content .rating .rating-wrapper {
  padding: 0 18px;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .user {
  position: absolute;
  top: 18px;
  right: 0;
  line-height: 12px;
  font-size: 0;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up,
.food-wrapper .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food-wrapper .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food-wrapper .food-content .rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


