
.cart-control {
  font-size: 0;
}
.cart-control .cart-decrease {
  display: inline-block;
  padding: 6px /* 增加可点击区域 -> 提高用户体验*/;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cart-control .cart-decrease .inner {
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cart-control .cart-decrease.move-enter-active,
.cart-control .cart-decrease.move-leave-active {
  -webkit-transform: translate3D(0, 0, 0) /*开启硬件加速*/;
          transform: translate3D(0, 0, 0) /*开启硬件加速*/;
}
.cart-control .cart-decrease.move-enter-active .inner,
.cart-control .cart-decrease.move-leave-active .inner {
  display: inline-block;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cart-control .cart-decrease.move-enter,
.cart-control .cart-decrease.move-leave-active {
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
  opacity: 0;
}
.cart-control .cart-decrease.move-enter .inner,
.cart-control .cart-decrease.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cart-control .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  font-size: 10px;
  line-height: 24px;
  text-align: center;
  color: #93999f;
}
.cart-control .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}


