
.goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods .menu-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
  position: relative;
}
.goods .menu-wrapper .menu-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.goods .menu-wrapper .menu-item.current {
  position: relative;
  margin-top: -1px;
  z-index: 10;
  background: #fff;
  font-weight: 700;
}
.goods .menu-wrapper .menu-item.current .text:after {
  display: none;
}
.goods .menu-wrapper .menu-item .text {
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  text-align: center;
}
.goods .menu-wrapper .menu-item .text .iconMap {
  vertical-align: top;
}
.goods .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: #93999f;
  background: #f3f5f7;
}
.goods .foods-wrapper .food-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  position: relative;
}
.goods .foods-wrapper .food-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.goods .foods-wrapper .food-item:last-child {
  margin-bottom: 0;
}
.goods .foods-wrapper .food-item:last-child:after {
  display: none;
}
.goods .foods-wrapper .food-item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  width: 57px;
  margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .food-item .content .name {
  margin: 2px 0 8px 0;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.goods .foods-wrapper .food-item .content .desc,
.goods .foods-wrapper .food-item .content .extra {
  margin-bottom: 8px;
  font-size: 10px;
  line-height: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .desc {
  line-height: 12px;
}
.goods .foods-wrapper .food-item .content .extra .count {
  margin-right: 12px;
}
.goods .foods-wrapper .food-item .content .price {
  font-weight: 700;
  line-height: 24px;
  font-size: 0;
}
.goods .foods-wrapper .food-item .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.goods .foods-wrapper .food-item .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .cart-control-wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
}


