
.rating-select .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  position: relative;
  font-size: 0;
}
.rating-select .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.rating-select .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  line-height: 16px;
  font-size: 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
}
.rating-select .rating-type .block.active {
  color: #fff;
}
.rating-select .rating-type .block .count {
  margin-left: 2px;
  font-size: 8px;
}
.rating-select .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.rating-select .rating-type .block.positive.active {
  background: #00a0dc;
}
.rating-select .rating-type .block.negative {
  background: rgba(77,85,93,0.2);
}
.rating-select .rating-type .block.negative.active {
  background: #4d555d;
}
.rating-select .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  font-size: 0;
}
.rating-select .switch.on .icon-check_circle {
  color: #00c850;
}
.rating-select .switch .icon-check_circle {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.rating-select .switch .text {
  font-size: 12px;
}


